<template>
  <div class="serchBox">
    <div class="serchb">
      <div class="serchInput">
        <i class="oneI"></i>
        <input
          type="text"
          v-model="something"
          @input="inputCut"
          @keyup.enter="KeyCallElse"
        />
        <label v-if="isLabel"></label>
        <i class="twoI" v-show="isX" @click="ICut"></i>
      </div>
    </div>

    <div class="one" v-if="isOne">
      <div class="serchli" v-if="isSearchHot">
        <h6>"{{ something }}"</h6>
        <ul @click="isOne = false">
          <li
            @click="matchingMusic(item.keyword)"
            v-for="(item, index) in SerchList"
            :key="index"
            @update:matchingMusicList="matchingMusicList = $event"
          >
            <i></i>
            <span>
              {{ item.keyword }}
            </span>
          </li>
          <!-- <SerchMusicItem
              :item="item"
              :index="index"
              v-for="(item, index) in SerchList"
              :key="index"
              @update:matchingMusicList="matchingMusicList = $event"
            ></SerchMusicItem> -->
        </ul>
      </div>

      <div class="searchHot" v-else>
        <h6></h6>
        <div
          class="hot"
          v-for="(item, index) in searchHot"
          :key="index"
          @click="
            callElse(item.searchWord);
            isX = true;
          "
        >
          {{ item.searchWord }}
        </div>

        <div class="record">
          <ul v-if="records[0] !== ''">
            <li v-for="(item, index) in records" :key="index">
              <i class="u-svg-histy"></i>
              <span @click='recordCut(item)'>{{item}}</span>
              <i class="u-svg-close" @click='deleteRecord(index)'></i>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <div class="two" v-else>
      <ul>
        <li
          v-for="(item, index) in matchingMusicList"
          :key="index"
          :currentMusic="$attrs.currentMusic"
          :paused="$attrs.paused"
          @click="
            $emit('update:music', { item, index });
            $emit('update:playlist', matchingMusicList);
            getCover(item.name);
          "
        >
          <div class="info">
            <h5>
              {{ item.name }}
            </h5>
            <p>
              <span class="sq"></span>
              <span>
                {{ item.name }}
              </span>
              <span class="zuozhe"> -{{ item.song.artists[0].name }} </span>
            </p>
          </div>
          <div class="icon">
            <span
              class="playing"
              v-if="currentMusic && currentMusic.id === item.id"
              :class="{ paused: paused }"
            >
              <i></i>
              <i class="one"></i>
              <i class="two"></i>
              <i></i>
            </span>
            <span class="play" v-else></span>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
// import SerchMusicItem from "@/components/SerchMusicItem.vue";
export default {
  props: ["SerchList", "searchHot", "currentMusic", "paused"],
  components: {
    // SerchMusicItem,
  },
  data() {
    return {
      // 
      something: null,
      //
      matchingMusicList: [],
      // 
      record: [],
      records: [],
      // 
      isOne: true,
      // label
      isLabel: true,
      // x
      isX: false,
      // 
      isSearchHot: false,
      // 
      countq: false,
    };
  },
  methods: {
    getCover: function (coverName) {
      this.$root.isShowLoading = true;
      this.axios.get("/search/multimatch?keywords=" + coverName).then((res) => {
        for (var k in this.matchingMusicList) {
          if (res.data.result.mv != undefined) {
            this.matchingMusicList[k].picUrl = res.data.result.mv[0].cover;
          } else {
            this.matchingMusicList[k].picUrl =
              "https://p2.music.126.net/6y-UleORITEDbvrOLV0Q8A==/5639395138885805.jpg";
          }
        }
        if (this.countq) {
          this.$root.isShowLoading = false;
        }
      });
    },
    // 
    matchingMusic: function (newVlue) {
      this.matchingMusicList = [];
      this.$root.isShowLoading = true;

      this.axios.get("/search?keywords=" + newVlue).then((response) => {
        // this.matchingMusicList = response.data.result.songs
        let hotlist = response.data.result.songs;
        // console.log(hotlist);

        for (var i in hotlist) {
          let HotPlaylistObj = {};
          HotPlaylistObj.name = hotlist[i].name;
          HotPlaylistObj.id = hotlist[i].id;
          HotPlaylistObj.picUrl = this.imgs;
          HotPlaylistObj.song = { artists: hotlist[i].artists };
          HotPlaylistObj.al = hotlist[i].artists[0].name;
          this.matchingMusicList.push(HotPlaylistObj);
        }
        // 
        if(this.record.indexOf(newVlue) == -1){
            this.record.unshift(newVlue)
            localStorage.setItem('record',this.record)
        }else{
          for(var k in this.records){
            if(this.records[k] == newVlue){
              this.records.splice(k,1);
              this.records.unshift(newVlue)
              localStorage.setItem('record',this.records)
            }
          }
        }
        this.records = localStorage.getItem("record").split(',');

        // something
        // console.log(111)
        if (this.countq) {
          this.$root.isShowLoading = false;
        }
      });
    },
    // 
    recordCut: function(newValue){

      this.isX = true;
      this.callElse(newValue)
    },
    // 
    deleteRecord:function(index){
      // if(!this.records){
        this.records.splice(index,1)
        localStorage.setItem('record',this.records)
      // }
    },
    // 
    callElse: function (newValue) {
      this.countq = true;
      this.matchingMusicList = [];
      // 
      this.something = newValue;
      this.isLabel = false;
      this.isOne = false;
      this.matchingMusic(newValue);
      // console.log(this.countq)
    },
    inputCut: function () {
      this.$emit("update:something", this.something);
      if (this.something == "") {
        this.isLabel = true;
        this.isX = false;
        this.isSearchHot = false;
        this.isOne = true;
      } else {
        this.isLabel = false;
        this.isX = true;
        this.isSearchHot = true;
      }
    },
    ICut: function () {
      this.something = null;
      this.isX = false;
      this.isLabel = true;
      this.isOne = true;
      this.isSearchHot = false;
    },
    // 
    KeyCallElse: function () {
      this.matchingMusicList = [];
      // 
      if (this.something == null) {
        this.isLabel = true;
      } else {
        this.isLabel = false;
        this.callElse(this.something);
      }
    },
  },
  created()  {
    this.records = localStorage.getItem("record").split(',');
    // console.log(this.records)
  },
  deactivated () {
    this.ICut();
  }
};
</script>
<style scoped lang="less">
.serchBox {
  width: 100%;
  .serchb {
    width: 100%;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    .serchInput {
      display: flex;
      position: relative;
      width: 92%;
      height: 30px;
      padding: 0 30px;
      box-sizing: border-box;
      background: #ebecec;
      border-radius: 30px;

      i.oneI {
        position: absolute;
        left: 0;
        top: 9px;
        margin: 0 8px;
        vertical-align: middle;
        width: 13px;
        height: 13px;
        background-image: url("data:image/svg+xml;base64,");
      }
      i.twoI {
        position: absolute;
        right: 7px;
        top: 50%;
        margin-top: -7px;
        width: 14px;
        height: 14px;
        background-image: url("data:image/svg+xml;base64,");
        background-position: 0 0;
        background-size: contain;
        background-repeat: no-repeat;
      }
      input {
        flex: 1;
        width: 100%;
        height: 30px;
        line-height: 18px;
        border-radius: 0;
        border: 0;
        background: rgba(0, 0, 0, 0);
        font-size: 14px;
        color: #333;
        outline: none;
      }
      label {
        position: absolute;
        left: 30px;
        top: 5px;
        font-size: 14px;
        color: #c9c9c9;
        background: rgba(0, 0, 0, 0);
        pointer-events: none;
      }
    }
  }
  .one {
    width: 100%;
    .serchli {
      width: 100%;
      h6 {
        height: 50px;
        margin-left: 10px;
        padding-right: 10px;
        font-size: 15px;
        line-height: 50px;
        color: #507daf;
        border-bottom: 1px solid rgba(0, 0, 0, 0.1);
        text-align: left;
      }
      ul {
        li {
          text-align: left;
          display: flex;
          height: 45px;
          padding-left: 10px;
          align-items: center;
          i {
            flex: 0 0 auto;
            margin-right: 7px;
            width: 15px;
            height: 15px;
            background-image: url("data:image/svg+xml;base64,");
            display: inline-block;
            vertical-align: middle;
            background-position: 0 0;
            background-size: contain;
            background-repeat: no-repeat;
          }
          span {
            flex: 1;
            padding-right: 10px;
            font-size: 15px;
            line-height: 45px;
            color: #333;
            border-bottom: 1px solid rgba(0, 0, 0, 0.1);
          }
        }
      }
    }
    .searchHot {
      width: 100%;
      text-align: left;
      padding: 10px;
      h6 {
        font-size: 12px;
        line-height: 12px;
        color: #666;
        margin-bottom: 10px;
      }
      div.hot {
        display: inline-block;
        height: 32px;
        box-sizing: border-box;
        margin-right: 8px;
        margin-bottom: 8px;
        padding: 0 14px;
        font-size: 14px;
        line-height: 32px;
        color: #333;
        border: 1px solid rgba(0, 0, 0, 0.1);
        border-radius: 60px;
      }
      .record {
        width: 100%;
        margin-top: 20px;
        ul {
          li {
            width: 100%;
            display: flex;
            align-items: center;
            height: 50px;
            line-height: 50px;
            border-color: rgba(0,0,0,.1);
            i{
              display: inline-block;
              vertical-align: middle;
              background-position: 0 0;
              background-size: contain;
              background-repeat: no-repeat;
            }
            span{
              padding-left: 10px;
              flex: 1;
              font-size: 14px;
              border-bottom: 1px solid rgba(0,0,0,.1);
              display: -webkit-box;
              -webkit-box-orient: vertical;
              -webkit-line-clamp: 1;
              overflow: hidden;
              text-overflow: ellipsis;
            }
            i.u-svg-histy {
              width: 15px;
              height: 15px;
              background-image: url("data:image/svg+xml;base64,");
            }
            i.u-svg-close{
              width: 12px;
              height: 12px;
              background-image: url('data:image/svg+xml;base64,');
            }
          }
        }
      }
    }
  }
  .two {
    ul {
      li {
        display: flex;
        border-bottom: 1px solid #f2f2f3;
        height: 54px;
        align-items: center;
        margin-left: 10px;
        .info {
          flex: 1;
          text-align: left;
          h5 {
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 1;
            overflow: hidden;
            text-overflow: ellipsis;
            width: 95%;
          }
          p {
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 1;
            overflow: hidden;
            text-overflow: ellipsis;
            color: #888;
            font-size: 12px;
            width: 95%;
          }
          .sq {
            display: inline-block;
            width: 12px;
            height: 8px;
            margin-right: 4px;
            background: url("https://s3.music.126.net/mobile-new/img/index_icon_2x.png?5207a28c3767992ca4bb6d4887c74880=")
              no-repeat;
            background-size: 166px 97px;
          }
          .zuozhe {
            font-size: 12px;
          }
        }
        .icon {
          width: 30px;
          height: 30px;
          text-align: center;
          line-height: 30px;
          .playing {
            display: flex;
            justify-content: space-between;
            align-items: flex-end;
            margin-right: 5px;
            // background: b;
            i {
              width: 4px;
              height: 30px;
              background: linear-gradient(
                to bottom,
                #ed6ea0,
                #ec8c69,
                #f7186a,
                #fbb03b
              );
              // display: inline-block;
              transform-origin: center bottom;
              animation: playing 0.6s linear -0.2s infinite alternate-reverse;
              &:first-of-type {
                animation-delay: 0s;
              }
              &:last-of-type {
                animation-delay: -0.6s;
              }
            }
            .one {
              animation-delay: -0.2s;
            }
            .two {
              animation-delay: -0.4s;
            }
            &.paused {
              i {
                animation-play-state: paused;
              }
            }
          }
        }
        .play {
          display: block;
          width: 23px;
          height: 23px;
          margin-top: 4px;
          background-position: -24px 0;
          background-image: url("https://s3.music.126.net/mobile-new/img/index_icon_3x.png?49a82fee3e0fc602e9c8b25df2bda159=");
          background-repeat: no-repeat;
          background-size: 166px 95px;
        }
      }
    }
  }
}
</style>